
import Header from "@/components/common/Header";
import IntegralRule from "@/components/layout/IntegralRule";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
name: "",
props: [""],
data() {
return {
TitleObjData: {
titleContent: "积分说明",
showLeftBack: true,
showRightMore: false
},
list: ["积分获取", "积分消耗"],
selItem: "积分获取",
imgUrl:
"http://kcapp.oss-cn-beijing.aliyuncs.com/appx/credit/credit-get.png"
};
},

components: {
Header,
IntegralRule,
Tab,
TabItem,
Sticky,
Divider,
XButton,
Swiper,
SwiperItem
},

computed: {
conHei() {
return { height: document.documentElement.clientHeight - 45 + "px" };
}
},

beforeMount() {},

mounted() {},

methods: {
tabItemClick(val) {
if (val === 0) {
this.imgUrl =
"http://kcapp.oss-cn-beijing.aliyuncs.com/appx/credit/credit-get.png";
} else {
this.imgUrl =
"http://kcapp.oss-cn-beijing.aliyuncs.com/appx/credit/credit-give.png";
}
}
},

watch: {}
};



